.form {
    max-width: 540px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.input {
    font-size: 24px;
    line-height: 32px;
    width: 540px;
    border-radius: 5px;
    border: 1px solid black;
    padding: 5px 10px;
}

@media screen and (max-width: 720px) {
    .input {
        max-width: 85%;
        margin: 0 auto;
    }
}